.setup-container {
  text-align: center;
  background-color: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  width: 600px;
  height: 630px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.setup-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 20px;
}

.input-group {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.input-group label {
  font-size: 18px;
  color: #2f3542;
  font-weight: bold;
}

.input-group input {
  padding: 12px;
  border: 2px solid #dfe4ea;
  border-radius: 5px;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

.input-group input:focus {
  outline: none;
  border-color: #5352ed;
}

.error-message {
  color: #ff4757;
  font-size: 16px;
  margin-top: 10px;
}

.start-button {
  background-color: #5352ed;
  color: white;
  border: none;
  padding: 15px 30px;
  font-size: 18px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: 20px;
}

.start-button:hover {
  background-color: #3742fa;
}

@media (max-width: 768px) {
  .setup-container {
    width: 100%;
    height: 100%;
    padding: 1rem;
    border-radius: 0;
    box-shadow: none;
  }

  .input-group input {
    padding: 10px;
    font-size: 14px;
  }

  .start-button {
    padding: 12px 24px;
    font-size: 16px;
  }
}
