.clues-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  height: 60px;
}

.clue {
  flex: 1;
  font-size: 18px;
  font-weight: bold;
  color: #2f3542;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 0 10px;
}

.bidirectional-arrow {
  width: 100px;
  height: 2px;
  background-color: #3742fa;
  position: relative;
  margin: 0 20px;
  flex-shrink: 0;
}

.bidirectional-arrow::before,
.bidirectional-arrow::after {
  content: "";
  position: absolute;
  top: 50%;
  width: 10px;
  height: 10px;
  border-top: 2px solid #3742fa;
  border-right: 2px solid #3742fa;
}

.bidirectional-arrow::before {
  left: 0;
  transform: translateY(-50%) rotate(-135deg);
}

.bidirectional-arrow::after {
  right: 0;
  transform: translateY(-50%) rotate(45deg);
}

@media (max-width: 768px) {
  .clues-container {
    flex-direction: row;
    height: auto;
    align-items: center;
    padding: 10px 0;
  }

  .clue {
    font-size: 14px;
    margin: 0;
    flex: 1;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    height: auto;
    min-height: 50px;
  }

  .bidirectional-arrow {
    width: 40px;
    margin: 0 10px;
  }
}
